<script>
  import { onMount } from "svelte";
  import showdown from "showdown";
  const converter = new showdown.Converter();
  const apiURL = "https://mobile.ladushki-school.org/data.json";
  //const apiURL = "http://localhost:5000/data.json";
  let data;

  function makeMD(ctx) {
    return converter.makeHtml(ctx);
  }
  onMount(async function() {
    let response = await fetch(apiURL, { method: "GET" });
    data = await response.json();
    data = data.map(x => '<div class="separation">' + makeMD(x) + "</div>");
  });
</script>

<style>
  :global(.separation) {
    padding: 20px;
    border: 1px white solid;
  }
</style>

{#await data then value}
  <div class="content">
    {@html value}
  </div>
{/await}
